<template>
  <transition
    name="dialog-fade"
    @after-enter="afterEnter"
    @after-leave="afterLeave">
    <div
      v-show="visible"
      ref="wrapper"
      :class="['el-dialog__wrapper', wrapperClass]"
      @mousedown.self="handleWrapperClick">
      <div
        role="dialog"
        :key="key"
        aria-modal="true"
        :aria-label="title || 'dialog'"
        :class="['el-dialog', { 'is-fullscreen': fullscreen, 'is-draggable': draggable, 'is-resizable': resizable, 'el-dialog--center': center }, customClass]"
        ref="dialog"
        :style="style">
        <div class="el-dialog__header" ref="header">
          <slot name="title">
            <span class="el-dialog__title">{{ title }}</span>
          </slot>
          <button
            type="button"
            class="el-dialog__headerbtn"
            aria-label="Close"
            v-if="showClose"
            @click="handleClose">
            <i class="el-dialog__close el-icon el-icon-close"></i>
          </button>
        </div>
        <div class="el-dialog__body" v-if="rendered"><slot></slot></div>
        <div class="el-dialog__footer" v-if="$slots.footer">
          <slot name="footer"></slot>
        </div>
        <div class="el-dialog__resizable" v-if="resizable">
          <i ref="resize"></i>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import Popup from 'element-gui/src/utils/popup';
  import Migrating from 'element-gui/src/mixins/migrating';
  import emitter from 'element-gui/src/mixins/emitter';

  export default {
    name: 'ElDialog',

    mixins: [Popup, emitter, Migrating],

    props: {
      title: {
        type: String,
        default: ''
      },

      modal: {
        type: Boolean,
        default: true
      },

      modalAppendToBody: {
        type: Boolean,
        default: true
      },

      appendToBody: {
        type: Boolean,
        default: false
      },

      lockScroll: {
        type: Boolean,
        default: true
      },

      closeOnClickModal: {
        type: Boolean,
        default: true
      },

      closeOnPressEscape: {
        type: Boolean,
        default: true
      },

      showClose: {
        type: Boolean,
        default: true
      },

      width: String,

      height: String,

      maxWidth: String,

      minWidth: String,

      maxHeight: String,

      minHeight: String,

      fullscreen: Boolean,

      draggable: Boolean,

      resizable: Boolean,

      wrapperClass: {
        type: String,
        default: ''
      },

      customClass: {
        type: String,
        default: ''
      },

      top: {
        type: String,
        default: '15vh'
      },
      beforeClose: Function,
      center: {
        type: Boolean,
        default: false
      },

      destroyOnClose: Boolean
    },

    data() {
      return {
        closed: false,
        key: 0
      };
    },

    watch: {
      visible(val) {
        if (val) {
          this.closed = false;
          this.$emit('open');
          this.$el.addEventListener('scroll', this.updatePopper);
          this.$nextTick(() => {
            const dialogDom = this.$refs.dialog;
            dialogDom.scrollTop = 0;
            dialogDom.focus();
          });
          if (this.appendToBody) {
            document.body.appendChild(this.$el);
          }
        } else {
          this.$el.removeEventListener('scroll', this.updatePopper);
          if (!this.closed) this.$emit('close');
          if (this.destroyOnClose) {
            this.$nextTick(() => {
              this.key++;
            });
          }
        }
      }
    },

    computed: {
      style() {
        let style = {};
        if (!this.fullscreen) {
          style.marginTop = this.top;
          if (this.width) {
            style.width = this.width;
          };
          if (this.height) {
            style.height = this.height;
          };
          if (this.maxWidth) {
            style.maxWidth = this.maxWidth;
          };
          if (this.minWidth) {
            style.minWidth = this.minWidth;
          };
          if (this.maxHeight) {
            style.maxHeight = this.maxHeight;
          };
          if (this.minHeight) {
            style.minHeight = this.minHeight;
          };
        }
        return style;
      }
    },

    methods: {
      getMigratingConfig() {
        return {
          props: {
            'size': 'size is removed.'
          }
        };
      },
      handleWrapperClick() {
        if (!this.closeOnClickModal) return;
        this.handleClose();
      },
      handleClose() {
        if (typeof this.beforeClose === 'function') {
          this.beforeClose(this.hide);
        } else {
          this.hide();
        }
      },
      hide(cancel) {
        if (cancel !== false) {
          this.$emit('update:visible', false);
          this.$emit('close');
          this.closed = true;
        }
      },
      updatePopper() {
        this.broadcast('ElSelectDropdown', 'updatePopper');
        this.broadcast('ElDropdownMenu', 'updatePopper');
      },
      afterEnter() {
        this.$emit('opened');
      },
      afterLeave() {
        this.$emit('closed');
      }
    },

    mounted() {
      if (this.visible) {
        this.rendered = true;
        this.open();
        if (this.appendToBody) {
          document.body.appendChild(this.$el);
        }
      };

      if (this.draggable && !this.fullscreen) {
        this.$refs.header.addEventListener('mousedown', e => {
          if (e.button !== 0) return;
          const dragDom = this.$refs.dialog;
          const headerDom = this.$refs.header;
          const wrapperDom = this.$refs.wrapper;

          const disX = e.clientX - headerDom.offsetLeft;
          const disY = e.clientY - headerDom.offsetTop;
          const screenWidth = wrapperDom.clientWidth;
          const screenHeight = wrapperDom.clientHeight;
          const dragWidth = dragDom.clientWidth;
          const dragHeight = dragDom.clientHeight;
          const minDragDomLeft = dragDom.offsetLeft;
          const maxDragDomLeft = screenWidth - dragDom.offsetLeft - dragWidth;
          const minDragDomTop = dragDom.offsetTop;
          const maxDragDomTop = screenHeight - dragDom.offsetTop - dragHeight;
          // 正则去掉px
          let styL = +dragDom.style.left.replace(/\px/g, '');
          let styT = +dragDom.style.top.replace(/\px/g, '');

          const handleMove = e => {
            let left = e.clientX - disX;
            let top = e.clientY - disY;
            // 边界处理
            if (-left > minDragDomLeft) {
              left = -minDragDomLeft;
            } else if (left > maxDragDomLeft) {
              left = maxDragDomLeft;
            };

            if (-top > minDragDomTop) {
              top = -minDragDomTop;
            } else if (top > maxDragDomTop) {
              top = maxDragDomTop;
            };

            dragDom.style.left = left + styL + 'px';
            dragDom.style.top = top + styT + 'px';
          };
          const cleanUp = () => {
            window.removeEventListener('mousemove', handleMove);
            window.removeEventListener('mouseup', handleMove);
            window.removeEventListener('mouseup', cleanUp);
          };
          window.addEventListener('mousemove', handleMove);
          window.addEventListener('mouseup', handleMove);
          window.addEventListener('mouseup', cleanUp);
        });
      };

      if (this.resizable && !this.fullscreen) {
        this.$refs.resize.addEventListener('mousedown', e => {
          if (e.button !== 0) return;
          const dragDom = this.$refs.dialog;
          const dragWidth = dragDom.clientWidth;
          const dragHeight = dragDom.clientHeight;
          const startPos = {
            x: e.pageX,
            y: e.pageY
          };
          const resizeMove = e => {
            dragDom.style.width = dragWidth + (e.clientX - startPos.x) * 2 + 'px';
            dragDom.style.height = dragHeight + e.clientY - startPos.y + 'px';
          };
          const cleanResizeUp = () => {
            window.removeEventListener('mousemove', resizeMove);
            window.removeEventListener('mouseup', cleanResizeUp);
          };
          window.addEventListener('mousemove', resizeMove);
          window.addEventListener('mouseup', cleanResizeUp);
        });
      };
    },

    destroyed() {
      // if appendToBody is true, remove DOM node after destroy
      if (this.appendToBody && this.$el && this.$el.parentElement) {
        this.$el.parentElement.removeChild(this.$el);
      }
    }
  };
</script>
