<template>
  <div class="el-collapse-item"
    :class="{'is-active': isActive, 'is-disabled': disabled}">
    <div
      role="tab"
      :aria-expanded="isActive"
      :aria-controls="`el-collapse-content-${id}`"
      :aria-describedby ="`el-collapse-content-${id}`"
    >
      <div
        class="el-collapse-item__header"
        @click="handleHeaderClick"
        role="button"
        :id="`el-collapse-head-${id}`"
        :tabindex="disabled ? undefined : 0"
        @keyup.space.enter.stop="handleEnterClick"
        :class="{
          'is-active': isActive
        }"
      >
        <slot name="title">{{title}}</slot>
        <em class="el-collapse-item__icon" @click.stop="handleIconClick">
          <template v-if="showText">{{isActive ? closeText : openText }}</template>
          <i class="el-collapse-item__arrow el-icon-arrow-right"
          :class="{'is-active': isActive}"></i>
        </em>
      </div>
    </div>
    <el-collapse-transition>
      <div
        class="el-collapse-item__wrap"
        v-if="(!lazy || loaded) || isActive"
        v-show="isActive"
        role="tabpanel"
        :aria-hidden="!isActive"
        :aria-labelledby="`el-collapse-head-${id}`"
        :id="`el-collapse-content-${id}`"
      >
        <div class="el-collapse-item__content">
          <slot></slot>
        </div>
      </div>
    </el-collapse-transition>
  </div>
</template>
<script>
  import ElCollapseTransition from 'element-gui/src/transitions/collapse-transition';
  import Emitter from 'element-gui/src/mixins/emitter';
  import { generateId } from 'element-gui/src/utils/util';
  import { t } from 'element-gui/src/locale';

  export default {
    name: 'ElCollapseItem',

    componentName: 'ElCollapseItem',

    mixins: [Emitter],

    components: { ElCollapseTransition },

    data() {
      return {
        contentWrapStyle: {
          height: 'auto',
          display: 'block'
        },
        contentHeight: 0,
        isClick: false,
        loaded: false,
        id: generateId()
      };
    },

    inject: ['collapse'],

    props: {
      title: String,
      iconTrigger: Boolean,
      iconText: Boolean,
      openText: {
        type: String,
        default() {
          return t('el.collapse.open');
        }
      },
      closeText: {
        type: String,
        default() {
          return t('el.collapse.close');
        }
      },
      name: {
        type: [String, Number],
        default() {
          return this._uid;
        }
      },
      disabled: Boolean,
      lazy: Boolean
    },

    computed: {
      triggerTarget() {
        return this.collapse.iconTrigger;
      },
      showText() {
        return this.collapse.iconText;
      },
      isActive() {
        const active = this.collapse.activeNames.indexOf(this.name) > -1;
        if (active) {
          this.loaded = true;
        };
        return active;
      }
    },

    methods: {
      handleHeaderClick() {
        if (this.disabled || this.triggerTarget) return;
        this.dispatch('ElCollapse', 'item-click', this);
        this.isClick = true;
      },
      handleEnterClick() {
        this.dispatch('ElCollapse', 'item-click', this);
      },
      handleIconClick() {
        if (this.disabled) return;
        this.dispatch('ElCollapse', 'item-click', this);
        this.isClick = true;
      }
    }
  };
</script>
